import {Map} from "maplibre-gl";
import jsPDF from "jspdf";
import nmConverter from "./nominatimMap";
import MapLibreGlWrapper from '../../MapLibreMap/lib/MapLibreGlWrapper'

const toPixels = (length:number):string => {
	let conversionFactor = 96;

	conversionFactor /= 25.4;

	return conversionFactor * length + "px";
};

const createPdf:Function = (map:MapLibreGlWrapper, locationValue:any, setLoading:any) => {
	setLoading(true);

	const width = 210;
	const height = 297;
	// Calculate pixel ratio
	const actualPixelRatio = window.devicePixelRatio;

	// Create map container
	const hidden = document.createElement("div");
	hidden.className = "hidden-map";
	document.body.appendChild(hidden);
	const container = document.createElement("div");
	container.style.width = toPixels(width);
	container.style.height = toPixels(height);
	hidden.appendChild(container);

	let style = map.map.getStyle();
	for (let name in style.sources) {
		let src = style.sources[name];

		Object.keys(src).forEach((key) => {
			//delete properties if value is undefined.
			// for instance, raster-dem might has undefined value in "url" and "bounds"
			if (!src[key]) {
				delete src[key];
			}
		});
	}
	//Render map
	var renderMap = new Map({
		container: container,
		center: map.map.getCenter(),
		zoom: map.map.getZoom(),
		bearing: map.map.getBearing(),
		pitch: map.map.getPitch(),
		interactive: false,
		preserveDrawingBuffer: true,
		fadeDuration: 0,
		attributionControl: false,
		style: style
	});

	renderMap.once("idle", function () {
		// TO DO: It is still under development
		const pdf = new jsPDF({
			orientation: "p",
			unit: "mm",
			compress: true,
		});
		Object.defineProperty(window, "devicePixelRatio", {
			get: function () {
				return 300 / 96;
			},
		});
		const offsetX = 2.5;
		const offsetY = 2.5;
		const marginTop = 3;
		const marginBottom = 3;
		const innerMargin = 2;
		const logo =
			"data:image/png;base64,";
		const textBuffer = 1;
		const lineHeight = 3.25;
		const text = locationValue ? nmConverter(locationValue.address) : "";
		const textChunksSeperator = text.split(",");
		const textChunks:string[] = [];

		if (textChunks.length) {
			textChunksSeperator.forEach((chunk:string) => {
				const limitChunks:(RegExpMatchArray | null) = chunk.match(/.{1,34}/g);
				if(limitChunks){
				textChunks.push(...limitChunks);
				}
			});
		}

		//Render map image
		pdf.addImage(
			renderMap.getCanvas().toDataURL("image/png"),
			"png",
			0,
			0,
			210,
			297,
			undefined,
			"FAST"
		);

		//Render lower left Copyright box
		pdf.setFillColor("white");
		pdf.rect(138, 287, 297, 10, "F");
		pdf.setFontSize(10); // optional
		pdf.text(
			"Datenquelle: © OpenStreetMap-Mitwirkende",
			140,
			pdf.internal.pageSize.height - 3
		);

		//Render infobox
		pdf.setFillColor("white");
		const infoBoxSize =
			textChunks.length * lineHeight +
			marginTop +
			marginBottom +
			lineHeight * 2 +
			innerMargin * 2 +
			textBuffer;

		pdf.rect(offsetX, 2, 66.5, infoBoxSize, "F");

		pdf.setFontSize(10);
		pdf.text("Karten PDF:", 6, offsetY + marginTop);

		//Render inner infobox
		pdf.rect(
			6,
			7,
			60,
			textChunks.length * lineHeight + innerMargin * 2 + textBuffer
		);
		pdf.setFontSize(10);

		//Write out address
		textChunks.forEach((text, i) => {
			pdf.text(text.trim(), 8, 10 + i * 3.5 + innerMargin);
		});

		//Add WG Logo
		pdf.addImage(
			logo,
			"png",
			5,
			offsetY + marginTop + lineHeight * 2 + textChunks.length * 3 + innerMargin * 2,
			3,
			3,
			undefined,
			"FAST"
		);

		//Add WG Url
		pdf.setFontSize(10);
		pdf.text(
			"wheregroup.com",
			40,
			offsetY +
				marginTop +
				lineHeight * 2 +
				textChunks.length * lineHeight +
				innerMargin * 2 +
				textBuffer
		);

		//Set pdfs props
		pdf.setProperties({
			title: "Map export",
			subject: "Map export",
			creator: "WhereGroup GmbH",
			author: "(c)WhereGroup GmbH, (c)OpenStreetMap",
		});

		pdf.save("Map.pdf");

		renderMap.remove();
		hidden.parentNode?.removeChild(hidden);
		Object.defineProperty(window, "devicePixelRatio", {
			get: function () {
				return actualPixelRatio;
			},
		});

		setLoading(false);
	});
};

export default createPdf;
